// Status.js
var Status = pc.createScript('status');

Status.attributes.add('dice', {type: 'entity', array : true});
Status.attributes.add('numberText', {type: 'entity'});
Status.attributes.add('gaugeImage', {type: 'entity'});
Status.attributes.add('winCover', {type: 'entity', array : true});

Status.prototype.initialize = function() {
    
    
};

Status.prototype.postInitialize = function() {
    this.init();
};

Status.prototype.init = function() {
    this.winCover.forEach( e => e.enabled = false);
    
    if (this.gaugeImage !== null) this.gaugeImage.element.height = 0; //max 300
    if (this.numberText !== null) this.numberText.element.text = `0`; 
    if (this.dice.length !== 0) this.dice.forEach( e => e.script.dice.setDiceNumber(0));

    this.sum = [0, 0];
};

Status.prototype.getWinCoverIndex = function(sum) {
    switch(sum){
        case 2:
        case 12 : 
            return 0;
        case 3:
        case 11: 
            return 1;
        case 4:
        case 10: 
            return 2;
        case 5:
        case 9: 
            return 3;
        default:
            return 4;
    }
};

Status.prototype.setWinner = function(sum) {
    if (this.winCover.length === 1){
        this.winCover[0].enabled = true;
    } else {
        let coverIndex = this.getWinCoverIndex(sum);
        this.winCover[coverIndex].enabled = true;
    }
};

Status.prototype.updateStatus = function() {
    const sum = this.sum.reduce(
        (accumulator, currentValue) => accumulator + currentValue,0 );

    if (this.gaugeImage !== null) this.gaugeImage.element.height = ( sum / 12 ) * 300; 
    if (this.numberText !== null) this.numberText.element.text = `${sum}`; 
};


Status.prototype.setDiceNumber = function(idx, number) {
    this.sum[idx] = number;
    if (this.dice.length !== 0) this.dice[idx].script.dice.setDiceNumber(number);

    this.updateStatus();
};







