// Bounce.js
var Bounce = pc.createScript('bounce');

Bounce.attributes.add('shadow',  {type: 'entity'});

Bounce.prototype.initialize = function() {
    this.onEnable();
    this.entity.on('enable', this.onEnable, this);
};

Bounce.prototype.onEnable = function() {  
    console.log('onEnable');
    var data = {
        value: 0
    };
    let delay = getRandomInt(6, 12) / 10;
    var tween = this.entity.tween(data).yoyo(true).loop(true).to({value: 15}, delay, pc.Linear);

    tween.onUpdate((dt) => {
        this.entity.setLocalPosition(0, data.value, 0);
        this.shadow.setLocalScale(1 - data.value / 100, 1 - data.value / 100, 1);
    });

    tween.start();      
};


