// Middle.js
var Middle = pc.createScript('middle');

Middle.attributes.add('blueDice', {type: 'entity', array : true});
Middle.attributes.add('blueDiceBg', {type: 'entity', array : true});

Middle.attributes.add('redDice', {type: 'entity', array : true});
Middle.attributes.add('redDiceBg', {type: 'entity', array : true});

//0 : blue, 1 : tie, 2 : red
Middle.attributes.add('status', {type: 'entity', array : true});

Middle.prototype.initialize = function() {
    Middle.instance = this;
    this.idleTimer = null;

    this.buttons = [];

    this.entity.forEach(e => {
        if (e.name ==='Button'){
            this.buttons.push(e);
        }
    });   

    this.selectedIdx = -1;     

    this.blueSum = 0;
    this.redSum = 0;
};

Middle.prototype.postInitialize = function() {
    this.init();
};

Middle.prototype.init = function() {
    this.blueDiceBg.forEach( e => e.enabled = false);
    this.redDiceBg.forEach( e => e.enabled = false);

    this.blueDice.forEach( e => e.script.dice.startRandom());
    this.redDice.forEach( e => e.script.dice.startRandom());

    this.status.forEach( e => e.script.status.init());
};

Middle.prototype.startGame = function() {
    this.init();
    
};

Middle.prototype.setDiceNumber = async function(isBlue, number, index) {
    let ttt = 500;

    let targetDiceBg = isBlue ? this.blueDiceBg : this.redDiceBg;
    let targetDice = isBlue ? this.blueDice : this.redDice;
    let status = isBlue ? this.status[0] : this.status[2];

    targetDiceBg[index].enabled = true;
    let delayTime = targetDice[index].script.dice.fadeStopRandom(number);

    await delay(delayTime + ttt);

    targetDiceBg[index].enabled = false;
    status.script.status.setDiceNumber(index, number);

    await delay(ttt);
};

Middle.prototype.setWinner = async function(winner, sum) {
    let target;
    if (winner === 'blue')      target = this.status[0];
    else if (winner === 'red')  target = this.status[2];
    else                        target = this.status[1];

    target.script.status.setWinner(sum);
};



