// Dice.js
var Dice = pc.createScript('dice');

Dice.attributes.add('bg',  {type: 'entity'});
Dice.attributes.add('dotsRoot',  {type: 'entity'});

// initialize code called once per entity
Dice.prototype.initialize = function() {
    this.dots = [];
    this.myNumber = 0;

    this.isRand = false;
    this.timer = 0;
    this.delayTime = 0.13;

    this.dotsRoot.children.forEach( e => {
        this.dots.push(e);
    });
};

Dice.prototype.update = function(dt) {
    let excludeArray = [this.myNumber];
    if (this.isRand){
        this.timer += dt;
        if (this.timer >= this.delayTime){
            let result = generateRandomNumbersInRange(1, 7, excludeArray, 1);
            this.setDiceNumber(result[0]);
            this.timer = 0;
        }
    }
};

Dice.prototype.startRandom = function() {
    this.isRand = true;
    this.timer = 0;
};

Dice.prototype.fadeStopRandom = function(number) {
    let delay = 2.7;
    this.timer = 0;
    var data = {
        value: 0.13
    };
    var tween = this.entity.tween(data).to({value: 1.2}, delay, pc.Linear);
    tween.start();

    tween.onUpdate((dt) => {
        this.delayTime = data.value;
    });

    tween.onComplete( () => {
        this.delayTime = 0.13;
        this.isRand = false;
        this.timer = 0;
        this.setDiceNumber(number);
    });

    return delay * 1000;
};

Dice.prototype.stopRandom = function(number) {
    this.isRand = false;
    this.timer = 0;
    this.setDiceNumber(number);
};

Dice.prototype.setColor = function(color) {
    this.bg.element.color = color;
};

Dice.prototype.setDiceNumber = function(num) {
    if ( 0 <= num && num <= 6){
        this.myNumber = num;
        let numPattern = dotsPattern[num];

        this.dots.forEach( e => {
            e.enabled = false;
        });

        for (let i = 0; i < this.dots.length; ++i){
            if (numPattern.includes(i))
            this.dots[i].enabled = true;
        }
    }
};

