// Twinkle.js
var Twinkle = pc.createScript('twinkle');

Twinkle.prototype.initialize = function() {
    this.onEnable();
    this.entity.on('enable', this.onEnable, this);

};

Twinkle.prototype.onEnable = function() {  
    var data = {
        value: 1
    };
    var tween = this.entity.tween(data).yoyo(true).loop(true).to({value: 0.5}, 0.3, pc.Linear);
    tween.onUpdate((dt) => {
        //console.log(data);
        this.entity.element.opacity = data.value;
    });

    tween.start();      

};


