// Bottom.js
var Bottom = pc.createScript('bottom');

Bottom.attributes.add('blueButton', {type: 'entity'});
Bottom.attributes.add('tieButton', {type: 'entity'});
Bottom.attributes.add('redButton', {type: 'entity'});
Bottom.attributes.add('betEditButton', {type: 'entity'});

Bottom.attributes.add('winResult', {type: 'entity'});
Bottom.attributes.add('multiplier', {type: 'entity'});
Bottom.attributes.add('profit', {type: 'entity'});
Bottom.attributes.add('loseResult', {type: 'entity'});
Bottom.attributes.add('resultDice', {type: 'entity', array : true});
Bottom.attributes.add('resultBg', {type: 'entity'});

Bottom.attributes.add('betAmountText', {type: 'entity'});

Bottom.attributes.add('betUi', {type:'entity'});

Bottom.prototype.initialize = function() {
    Bottom.instance = this;

    this.setButton(this.blueButton, this.onClickBlue);
    this.setButton(this.tieButton, this.onClickTie);
    this.setButton(this.redButton, this.onClickRed);
    this.setButton(this.betEditButton, this.onClickBetEdit);

    this.disableButton(false);

    this.betAmount = 0;
};

Bottom.prototype.postInitialize = function(){
    this.resultBg.enabled = false;
    this.winResult.enabled = false;
    this.loseResult.enabled = false;
}

Bottom.prototype.setButton = function(btn, clickHandler){
    btn.button.on('touchend', clickHandler, this);
    btn.button.on('mouseup', clickHandler, this);
};

Bottom.prototype.startGame = async function(betType){
    AudioController.instance.playSound('Click');
    
    this.disableButton(true);

    if (UserBalance.instance.userBalance < this.betAmount){
        this.betAmountErr();

        await delay(1000);

        this.disableButton(false);
    }
    else {
        GameController.instance.betGame(this.betAmount, betType);
    }
};

Bottom.prototype.onClickBlue = function(){
    AudioController.instance.playSound('Click');
    this.tieButton.enabled = false;
    this.redButton.enabled = false;

    this.startGame('blue');
};

Bottom.prototype.onClickTie = async function(){
    AudioController.instance.playSound('Click');
    this.blueButton.enabled = false;
    this.redButton.enabled = false;

    this.startGame('tie');
};

Bottom.prototype.onClickRed = async function(){
    AudioController.instance.playSound('Click');
    this.blueButton.enabled = false;
    this.tieButton.enabled = false;

    this.startGame('red');
};

Bottom.prototype.onClickBetEdit = async function(){
    AudioController.instance.playSound('Click');

    this.betUi.enabled = true;
};

Bottom.prototype.changeTexture = function(target, texture){
    //target.element.texture = texture.resource;
};


Bottom.prototype.changeButtonState = function(target, isActive){
    target.button.active = isActive;
};

Bottom.prototype.disableButton = function(isDisable) {
    if (isDisable === false){
        this.blueButton.enabled = true;
        this.tieButton.enabled = true;
        this.redButton.enabled = true;
    }
    this.changeButtonState(this.blueButton, !isDisable);
    this.changeButtonState(this.tieButton, !isDisable);
    this.changeButtonState(this.redButton, !isDisable);
    this.changeButtonState(this.betEditButton, !isDisable);
};

Bottom.prototype.setBet = function() {
    //this.disableAll();
    this.betUi.enabled = true;
};

Bottom.prototype.setIdle = function() {
    UiStats.instance.buttonActive(true);
    this.clearButton.enabled = false;
    this.randomButton.enabled = false;
    //this.disableAll();

    setTimeout( () => {
        this.changeButtonState(this.startButton, true);
        this.changeTexture(this.startButton, this.start_inactive);
    }, 1000);
};

Bottom.prototype.setBetAmount = function(amount) {
    this.betAmountText.element.text = `${amount}`;
    this.betAmount = amount;
};


Bottom.prototype.resetMultiplierColor = function() {
    ResultController.instance.resetColor();

    this.disableButton(false);
};

Bottom.prototype.setResultGame = async function(isWin, multiplier, profit, winner, numbers) {
    if (isWin){
        AudioController.instance.playSound('Win');
        this.multiplier.element.text = `${multiplier.toFixed(2)}x`;
        this.profit.element.text = `+${profit}`;
        if (winner === 'tie'){
            this.resultDice.forEach( e => e.enabled = false );
        }
        else {
            this.resultDice.forEach( (e, index) => { 
                e.enabled = true;
                e.script.dice.setColor( getColor(winner) );
                console.log(index + '    ' + numbers[index]);
                e.script.dice.setDiceNumber( numbers[index]);
            });
        }
    }
    else{
        AudioController.instance.playSound('Lose');
    }

    this.resultBg.enabled = true;
    this.winResult.enabled = isWin;
    this.loseResult.enabled = !isWin;

    await delay(2000);

    this.resultBg.enabled = false;
    this.winResult.enabled = false;
    this.loseResult.enabled = false;
};

Bottom.prototype.betAmountErr = function(){
    var color2 = new pc.Color(0, 0, 0);
    var tween = this.entity.tween(color2).yoyo(true).repeat(4).to(new pc.Color(1, 0, 0), 0.2, pc.Linear);
    tween.onUpdate((dt) => {
        this.betAmountText.element.color = color2;
    });

    tween.start();
}

