function getRandomInt (min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min; 
}

function generateRandomNumbersInRange(min, max, excludeArray, count) {
    const result = [];

    while (result.length < count) {
        const randomNum = Math.floor(Math.random() * (max - min)) + min;
        if (!excludeArray.includes(randomNum)) {
            result.push(randomNum);
        }
    }

    return result;
}

function getColor(type){
    if (type === 'blue')     return rgbToColor(45, 92, 255, 255);
    else if (type === 'red') return rgbToColor(237, 57, 57, 255);
    else                     return rgbToColor(241, 208, 63, 255);
}

function getBlueColor() {
    return rgbToColor(45, 92, 255, 255);
};

function getRedColor() {
    return rgbToColor(237, 57, 57, 255);
};

function setButton(btn, handler, entity) {
    btn.element.on('touchend', handler, entity);
    btn.element.on('mouseup', handler, entity);
}

function rgbToColor(r, g, b, a) {
    return new pc.Color(r/255, g/255, b/255, a/255);
}

function getCommaText(number) {
    let num = number;
    let commas = num.toLocaleString("en-US");

    return commas;
}

