// DummyServer.js
var DummyServer = pc.createScript('dummyServer');

DummyServer.prototype.initialize = function() {
    DummyServer.instance = this;

    this.betAmount = -1;
    this.userBalance = 0;
};

DummyServer.prototype.login = async function() {
    this.userBalance = getRandomInt(1000, 5000);
    //let json = await loadJsonFromUrl(`https://randomuser.me/api/`);
    
    return {
        id : 'userName',
        balance : this.userBalance,
    };
};

DummyServer.prototype.getDice = function() {
    let dice = [];
    dice.push(getRandomInt(1, 7));
    dice.push(getRandomInt(1, 7));

    const sum = dice.reduce(
        (accumulator, currentValue) => accumulator + currentValue,0 );

    return {
        dice : dice,
        sum : sum
    };
};

DummyServer.prototype.getResult = function(blueDice, redDice, betType) {
    let multiplier = 0;
    let isWin = false;
    let winner =  '';

    if (blueDice.sum === redDice.sum){
        winner = 'tie';
        if (winner === betType){
            isWin = true;
            multiplier = tieRate[blueDice.sum];
        }
    }
    if (blueDice.sum > redDice.sum) {
        winner = 'blue';
        if (winner === betType){
            isWin = true;
            multiplier = 2;
        }
    }
    if (blueDice.sum < redDice.sum) {
        winner = 'red';
        if (winner === betType){
            isWin = true;
            multiplier = 2;
        }
    }

    return {
        multiplier : multiplier,
        isWin : isWin,
        winner : winner
    };
};

//betType : 'blue', 'tie', 'red'
DummyServer.prototype.startGame = function(betAmount, betType) {
    if (betAmount < 0)
        return;
    if ( ! (betType === 'blue' || betType ==='tie' || betType === 'red'))
        return;

    let blueDice = this.getDice();
    let redDice = this.getDice();

    let result = this.getResult(blueDice, redDice, betType);
    this.userBalance = this.userBalance - betAmount;
    this.betAmount = betAmount;

    let profit = result.multiplier * betAmount;

    this.userBalance = this.userBalance + profit;

    let betReturn = {
        balance : this.userBalance,
        betAmount : this.betAmount,
        profit : profit,
        userBet : betType,
        blue : blueDice,
        red : redDice,
        ...result
    };
    
    return betReturn;
};

