// GameController.js
var GameController = pc.createScript('gameController');

GameController.prototype.initialize = function() {
    GameController.instance = this;
};

GameController.prototype.postInitialize = function() {
    this.init();

    this.setIdle();
};

GameController.prototype.init = async function() {
    let loginInfo = await DummyServer.instance.login();
    console.log(loginInfo);

    UserBalance.instance.setBalance(loginInfo.balance);
    UserBalance.instance.setUserName(loginInfo.id);
};

//betType : 'blue', 'tie', 'red'
GameController.prototype.betGame = async function(betAmount, betType) {
    let result = DummyServer.instance.startGame(betAmount, betType);

    UserBalance.instance.setBalance(UserBalance.instance.getUserBalance() - betAmount);
    await delay(150);

    //blue 0 open
    await Middle.instance.setDiceNumber(true, result.blue.dice[0], 0);

    //red 0 open
    await Middle.instance.setDiceNumber(false, result.red.dice[0], 0);
    
    if (result.blue.dice[0] <= result.red.dice[0]){
        // blue <= red => blue 1 open, red 1 open
        await Middle.instance.setDiceNumber(true, result.blue.dice[1], 1);
        await Middle.instance.setDiceNumber(false, result.red.dice[1], 1);
    }
    else{
        // blue > red => red 1 open, blue 1 open
        await Middle.instance.setDiceNumber(false, result.red.dice[1], 1);
        await Middle.instance.setDiceNumber(true, result.blue.dice[1], 1);
    }

    await delay(500);
    let sum = result.winner === 'red' ? result.red.sum : result.blue.sum;
    Middle.instance.setWinner(result.winner, sum);

    await delay(500);
    Bottom.instance.setResultGame(result.multiplier !== 0, result.multiplier, result.profit,
                                    result.winner, result.winner === 'blue'? result.blue.dice : result.red.dice);
    await delay(500);
    
    UserBalance.instance.setBalance(result.balance);
    await delay(2000);

    Bottom.instance.disableButton(false);

    Middle.instance.init();
    return;
};

GameController.prototype.setIdle = function() {
    //Middle.instance.setIdle();
    //Bottom.instance.setIdle();
};


